import React, { useState, useEffect } from "react";
import Web3Connect from "./Web3Connect";
import UBToken from "../contracts/UBToken.json";
import CryptoJS from 'crypto-js';
import "../App.css";

const WalletPanel = ({ walletAddress, tokenBalanceObj }) => {
  const { web3, contract } = Web3Connect();
  const [selectedTab, setSelectedTab] = useState('assets');
  const [ucpRecords, setUcpRecords] = useState([]);
  const [showTransferForm, setShowTransferForm] = useState(false);
  const [recipient, setRecipient] = useState('');
  const [amount, setAmount] = useState('');

  const handleTabChange = (tab) => {
    setSelectedTab(tab);
  };

  const tabs = ['', '', '', '', ''];

  const tabButtonStyle = {
    padding: '10px 15px',
    borderRadius: '5px',
    margin: '0 5px',
    cursor: 'pointer',
    backgroundColor: '#0366d6',
    color: '#fff',
    border: 'none',
    outline: 'none',
  };

  const contentStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    fontFamily: 'Arial, sans-serif',
    padding: '20px',
    border: '2px solid #0366d6',
    borderRadius: '10px',
    backgroundColor: '#f6f6f6',
  };

  const balanceStyle = {
    fontSize: '24px',
    fontWeight: 'bold',
    margin: '20px 0',
  };

  const tokenListStyle = {
    textAlign: 'left',
    marginTop: '20px',
  };

  const tokenItemStyle = {
    border: '1px solid #ccc',
    borderRadius: '5px',
    padding: '20px',
    marginBottom: '20px',
    display: 'flex',
    alignItems: 'center',
  };

  const getData = async () => {
    try {
      const ucpRecords = await contract.methods.getUCPs(walletAddress).call();
      setUcpRecords(ucpRecords);
    } catch (error) {
      console.error(' :', error);
    }
  };

  const renderTokens = (tokenList, contractAddress) => {
    return tokenList.map((token, index) => {
      const stringValue = `${contractAddress}${token.ucpName}${token.ucpDescription}`;
      const ucpHashValue = CryptoJS.SHA256(stringValue).toString(CryptoJS.enc.Hex).slice(0, 38);

      return (
        <div key={index} style={tokenItemStyle}>
          <img
            src="/images/ub_logo.PNG"
            alt="Token Image"
            style={{ width: '50px', height: '50px', borderRadius: '50%', marginRight: '20px' }}
          />
          <div>
            <div><b> :</b> {contractAddress}</div>
            <div><b>- :</b> {"0x" + ucpHashValue}</div>
            <br/>
            <div><b> :</b> {token.ucpName}</div>
            <div><b> :</b> {token.ucpDescription}</div>
            <div><b>:</b> {token.confirmed ? '' : ' '}</div>
            <br/>
            <button> </button>
            <button>  -</button>
            <button> -   </button>
          </div>
        </div>
      );
    });
  };

  const handleNFTButtonClick = async () => {
    setSelectedTab('');
    setShowTransferForm(false);
    await getData();
  };

  const handleTokenButtonClick = async () => {
    setSelectedTab('tokens');
    setShowTransferForm(false);
    await getData();
  };

  const handleTransferTokenETH = async () => {
    setSelectedTab('assets');
    setShowTransferForm(false);
    alert("In development");
  }

  const handleTransferTokenUBT = () => {
    setSelectedTab('assets');
    setShowTransferForm(true);
  }

  const handleTransferUBT = async () => {
    try {
      //      UBT,  recipient  amount
      //      
      //    
      setShowTransferForm(false);
    } catch (error) {
      console.error('   UBT:', error);
    }
  }

  return (
    <div style={{ display: 'flex', justifyContent: 'center', paddingTop: '20px' }}>
      <div style={{ width: '800px' }}>
        <div style={{ display: 'flex', justifyContent: 'center', backgroundColor: '#f6f6f6', padding: '10px', borderRadius: '10px 10px 0 0' }}>
          {tabs.map(tab => (
            <button
              key={tab}
              onClick={() => handleTabChange(tab)}
              style={{
                ...tabButtonStyle,
                backgroundColor: selectedTab === tab ? '#0366d6' : '#ccc',
              }}
            >
              {tab}
            </button>
          ))}
        </div>
        <div style={contentStyle}>
          <h2>  {selectedTab === 'assets' ? 'assets' : selectedTab} </h2>
          <h3> : {walletAddress}</h3>

          <div style={balanceStyle}>
            : {tokenBalanceObj} UBT
          </div>
          {selectedTab === '' && (
            <div style={tokenListStyle}>
              <h2> </h2>
              {renderTokens(ucpRecords, contract._address)}
            </div>
          )}
          {selectedTab === 'tokens' && (
            <div>
              <h3> -</h3>
            </div>
          )}
          {showTransferForm && (
            <div>
            <h3> UBT</h3>
            <div>
              <label> : </label>
              <input type="text" value={recipient} onChange={(e) => setRecipient(e.target.value)} />
            </div>
            <div style={{ marginTop: '10px' }}>
              <label> UBT: </label>
              <input type="number" value={amount} onChange={(e) => setAmount(e.target.value)} />
            </div>
            <br></br>
            <button onClick={handleTransferUBT}></button>
          </div>
          
          )}
          <div style={{ display: 'flex', justifyContent: 'center', marginTop: '20px' }}>
            <button onClick={handleNFTButtonClick} style={tabButtonStyle}> </button>
            <button onClick={handleTokenButtonClick} style={tabButtonStyle}> </button>
            <button onClick={handleTransferTokenETH} style={tabButtonStyle}> ETH</button>
            <button onClick={handleTransferTokenUBT} style={tabButtonStyle}> UBT</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default WalletPanel;
